/*匯入設定*/ 
@import"variable.scss";
@import "reset.scss";
@import"layout.scss";
@import"grid.scss";

.series-list{
    display: flex;
    li   {
        width: 33.3%;
        border: 1px solid #DCDCDC; 
    }
    .active{ 
        border-bottom:8px solid #AA0601; 
    }
    a{
        text-decoration: none;
        font-size: $h4-font-size;
        color: #000;
        padding: 32px 9px;
        display: block;
        text-align: center;
        font-weight: bold;
    }  
}



.series-list .material-icons-outlined {
    font-size: 34px;
    margin-right: 10px;
    display: inline-block;
    font-weight: bold;
}

.series-banner{
    display: flex;
    .col-1{
        background: url(../images/banner-1.png) no-repeat;
       }

    .col-2{
        background: url(../images/banner-2.png) no-repeat;
       }
    }

    .series-banner .col-1,.series-banner .col-2{
        background-size: cover;
        height: 304px;
        width: 50%;
       }

.series-body{
    @include h2-text-padding-mold 
    h2.title{ 
     font-size: $h3-font-size;
     margin-bottom: 16px;
    }
    p{
     font-size: $h4-font-size;
    }
}

.series-items{
    margin-top: 40px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    li{
        width: 23%;
    }
    img{
        height: 164px;
        object-fit: cover;
    }
    .content {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;
    }
}

.type,.price{
    width: 50%;
    font-size: $h6-font-size;
    font-weight: bold;
    margin: 8px 0;
}

.content{
    .price{
      color: $font-third-color;
    }      
}

ul.series-color li {
      display: inline-block;
      width: 24px;
      height: 24px;
      border-radius: 4px;
      margin-bottom: 104px; 
}

 ul.series-color li.watch-color-first {
     background: #5F3E2D;  
 }

ul.series-color li.watch-color-sec {
    background: #B75929;
    
}
    

